@media screen and (max-width: 480px){
  .title{
    font-size: 20px;
    margin-top: -100px;
  }
  
  header{
    justify-content: left;
  }

  .hometab img{
    width: 80px;
    height: auto;
  }
  
  .hometab img:hover{
    width: 80px;
    height: auto;
    margin-left: 0px;
    transform: rotate(0);
  }
  nav{
    display: flex;
    margin-top: 60px;
  }
  ul{
    margin-top: -30px;
  }
  ul li{
    font-size: 15px;
  }
  header{
    height: 250px;
    flex-direction: row;
    
  }
  .borderref{
    margin-top: 0;
  }
  #previewimage0,#previewimage1, #previewimage2, #previewimage3, #previewimage4, #previewimage5, #previewimage6, 
  #previewimage7, #previewimage8, #previewimage9, #previewimage10, #previewimage11, #previewimage12{
    width: 100%;
    margin-left: 0px;
    top: 0px;
  }
  nav ul{
    display: none;
  }
 
  .footerlinks{
    display: flex;
    flex-direction: column;
    align-items: center;
    
  }
  .footername1, .footername2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    align-items: center;
   
  }
  .footertitle{
    display: flex;
    align-items: center;
    margin-top: 40px;
    font-size: 6vw;
  }
  .footerlove1{
    margin-top: 20px;
  }
  .sitesocials1, .devsocials1{
    display: flex; 
    justify-content: space-evenly;
    width: 40%;
  }
  .footername2{
    margin-top: 50px;
  }

  .menuicon img{
    display: inline-block;
    
  }
  .menuicon{
    margin-top: -250px;
    float:right;
    width: 73vw;
    display: flex;
    justify-content: right;

    
  }

  #carticon{
    width: 35px;
    padding-right: 10px;
  
  }


 
}